<template>
  <modal v-model="visible" title="配置" class-name="vertical-center-modal" 
    @on-visible-change="onVisibleChange" :closable="false"
  >
    <!-- <div :style="{height: height + 'px'}"> </div>-->
    <textarea ref="textarea" :style="{height: height + 'px'}" />
    
    <div slot="footer" >
      <i-button type="error" @click="$emit('on-close')">取消</i-button>
      <i-button type="primary" @click="onOK">確定</i-button>
    </div>
  </modal>
</template>
<script>
import CodeMirror from "codemirror";
import "codemirror/addon/lint/lint.css";
import "codemirror/lib/codemirror.css";
import "codemirror/theme/rubyblue.css"; // 主題顯示，可以百度搜索其他的主題
require("script-loader!jsonlint");
import "codemirror/mode/javascript/javascript";
import "codemirror/addon/lint/lint";
import "codemirror/addon/lint/json-lint";

export default {
  name: 'ModalConfig',
  props: ["visible"],
  components: {CodeMirror},
  data () {
    return {
      height: document.body.clientHeight - 100
    }
  },
  async mounted() {
    this.value = [
      {
      items: [
      { market_type: "forexdata", symbol: "XAUUSD" },
      { market_type: "forexdata", symbol: "UKOIL" },
      { market_type: "forexdata", symbol: "CORN" }
      ],
      name: ""
      }
    ];
    // CodeMirror的配置項，搜官網看這裡的配置項配置
    this.jsonEditor = CodeMirror.fromTextArea(this.$refs.textarea, {
      lineNumbers: true, // 是否顯示行數
      mode: "application/json",  // 接受的類型，json xml....
      gutters: ["CodeMirror-lint-markers"], // 樣式的寬度
      theme: "rubyblue", // 主題
      lint: true
    });
    this.jsonEditor.setValue(JSON.stringify(this.value, null, 2));
    this.jsonEditor.on("change", cm => {
    //   this.$emit("changed", cm.getValue());
    // 編輯json框裡面的內容可以時刻監聽到值，通過cm.getValue()獲取到
    });
    setTimeout(()=>{
      let modal = document.querySelectorAll(".ivu-modal");
      for(let i = 0; i < modal.length; i++) {
        if(modal[i].style.display != "none") {
          modal[i].style.top = "20px";
          // let tabScript = document.querySelector("#tab-script");
          // let textarea = document.querySelector("#scriptValue textarea");
          // textarea.style.height = (tabScript.clientHeight -8) + "px";
        }
      }
    }, 300)
  },
  methods: {
    onOK(){
      
    },
    onVisibleChange(v) {
      if (v === false) {
        this.$emit("on-close");
      }
    },
  },
  watch: {
    visible(value){
    },
    editItem(value) {
    }
  }
}
</script>


